.header__list {
  @include vp-1199 {
    display: none;
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

.header__list--nojs {
  @include vp-1199 {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.5);
  }
}
